<template>
<div>
    <h2 class="content-block">Seleção de Período</h2>
    <div class="content-block dx-card responsive-paddings">
                      
    <div id="form-demo">
    <div class="widget-container">
    <form
        action="your-action"
        @submit="handleSubmit"
      >
        <DxForm
          :form-data="datas"          
          :read-only="false"
          :show-colon-after-label="true"
          
        >
          <DxGroupItem caption="Período">
            <DxSimpleItem
              :editor-options="dateBoxOptions"
              data-field="dt_inicial"
              editor-type="dxDateBox"
            >
              <DxLabel text="Data Inicial"/>
              <DxRequiredRule message="Data Inicial é obrigatória"/>
            </DxSimpleItem>

            <DxSimpleItem
              :editor-options="dateBoxOptions"
              data-field="dt_final"
              editor-type="dxDateBox"
            >
              <DxLabel text="Data Final"/>
              <DxRequiredRule message="Data Final é obrigatória"/>
            </DxSimpleItem>

          </DxGroupItem>
          <DxButtonItem
            :button-options="buttonOptions"
            horizontal-alignment="left"
          />
        </DxForm>
      </form>
    </div>
  </div>
 </div>
</div>
</template>
<script>

import DxForm, {
  DxGroupItem,
  DxSimpleItem,
  DxButtonItem,
  DxLabel,
  DxRequiredRule
} from 'devextreme-vue/form';

import notify from 'devextreme/ui/notify';
import ptMessages from "devextreme/localization/messages/pt.json";
import { locale, loadMessages } from "devextreme/localization";
import config from 'devextreme/core/config';

var datareg = {
    'dt_inicial' : '',
    'dt_final' : ''
}

export default {
  components: {
    DxGroupItem,
    DxSimpleItem,
    DxButtonItem,
    DxLabel,
    DxRequiredRule,
    DxForm,
    notify
  },

  data() {
    return {

      datas      : {},

      buttonOptions: {
        text: 'Confirmar',
        type: 'success',
        useSubmitBehavior: true
      },
      dateBoxOptions: {        
        invalidDateMessage:
          'A data deve ser no format: dd/mm/yyyy'
        
      }
    };
  },

  created() {
     //locale(navigator.language);
    config({ defaultCurrency: 'BRL' });  
    loadMessages(ptMessages);
    locale(navigator.language);     

   // locale('pt-BR');           
      //this.dt_inicial    = localStorage.dt_inicial;
      //this.dt_final      = localStorage.dt_final;

      datareg.dt_inicial = localStorage.dt_inicial;
      datareg.dt_final   = localStorage.dt_final;
  
     // datareg.dt_inicial = '01/01/2020';
     // datareg.dt_final   = '01/01/2020';
 
      this.datas         = datareg;

      //alert(datareg.dt_inicial);
      //alert(datareg.dt_final); 
 
  },     

  methods: {
    handleSubmit(e) {
     // alert(this.datas.dt_final);
      notify({
        message: 'Período Selecionado !',
        position: {
          my: 'center top',
          at: 'center top'
        }
      }, 'success', 500);

      e.preventDefault();

      //alert(typeof(datareg.dt_inicial));  

      localStorage.dt_inicial = datareg.dt_inicial;
      localStorage.dt_final   = datareg.dt_final;

      //alert(datareg.dt_inicial);
      //alert(datareg.dt_final);
      //alert(typeof(datareg.dt_final));

      //
      this.$router.push({
        path: "/home",
        query: { redirect: this.$route.path }
      });

    }
  }
};
</script>

<style scoped>
form {
  margin: 10px;
}
</style>
